
.navbar{
  color: black;
   background-color: white;
   width: 30%;
   font-weight: 300;
   position: fixed;
   top: 90px;
   left: -465px;
   background-color: #ffffffeb;
   display: flex;
   z-index: 2;
   overflow-y: auto;
   transition: 0.5s all;

   border: 1px solid #4a4a4a;
   left: -100%;
   -webkit-backdrop-filter: blur(3px);
   backdrop-filter: blur(3px);

}
   
   @keyframes fadein {
    0%{opacity:0;}
    100%{opacity:1;}
    }
    .hidden{
    display: flex;
    animation: gotoleft 0.5s forwards;
    transform:translateX(0)
    }
    
    
    @keyframes gotoleft{
    
    0%{
      transform:translateX(0)
    }
    100%{
    
      transform:translateX(-200%)
    }
    }
    .show{
    
    animation: fromleft 0.5s forwards;
    left: 93px;
    transition: 0.5s all ;
    }
    @keyframes fromleft{
    
    0%{
      transform:translateX(-100%)
    }
    100%{
    
      transform:translateX(0)
    }
    }
