
.navitemsholder .item{
    margin: auto;
    color: black;
    display: inline;


}
.navitemsholder .item_a_conatiner{
 
  display: inline;
position: relative;

}
.navitemsholder .item_a_conatiner:after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: -4px;
  left: 0;
  background-color: black;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.navitemsholder .item_a_conatiner:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
.navitemsholder .item a{
 
    color: #033072;


}

.navitemsholder .item:hover{
    color: rgb(46, 46, 46);
    cursor: pointer;
    }
    .navitemsholder .item:hover .navitemsection{
        display: block;
        }
    

.navitemsholder{
    display: flex;
    margin-left: auto;
    margin-right: 20px;
    position: relative;
    background-color: white;
    padding: 0.5rem;
    border-radius: 15px;
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
    width: 568px;
    z-index: 5;
    padding-bottom: 16px;
    padding-top: 15px;
    height: fit-content;
    margin-top: 20px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border: 1px solid #e1e1e1;

}
.navitemsection {
  display: none;
    position: absolute;
    background-color: transparent;
    padding-top: 10px;

}
.navitem a:hover,label:hover{

  color: rgb(96, 96, 96);
  cursor: pointer;
  
  }
  .navitem label:hover{

    color: rgb(96, 96, 96);
    cursor: pointer;
    
    }

  
.navitem_subsection{
    padding: 0.5rem;
    background: white;
  
    -webkit-backdrop-filter: saturate(180%) blur(20px);
backdrop-filter: saturate(180%) blur(20px);
text-align: left;
}

@media only screen and (max-width: 800px){
.navitemsholder{

  display: none;
}
body{

  background-color: white;
}


}


