.blockquote{

    margin-left: auto;
    display: flex;
    right: 0;
 
    width: fit-content;

    border-radius: 56px;
    padding-right: 34px;
    padding-left: 16px;
  }
  .blockquote svg{
    width: 48px;
    height: 23px;
    margin-top: 15px;
}
.quote-icon {
    margin-bottom: 10px;
  }
  .quote {
    max-width: 400px;
    line-height: 1.4;
    font-style: italic;
    color: #fff;
    
  }
  path{

    fill: black;
  
   }
.blockquote hr {
    margin-top: 50px;
  }
  .blockquote p{
    text-align: end;
    margin-left: 20px;
    font-size: 16px;
    color: white;
    margin-bottom: 5px;
    
  }
   .live-date-time{
    margin-top: 0px;
   }
    @media only screen and (max-width:800px) {
  .blockquote{
      padding: 0px;
      margin-right: 20px;
      position: absolute;
      }
      .blockquote svg{
        margin: auto;
        width: 20px;
    
      }
      .blockquote p {
        
        
        height: 25px;
      
        margin: auto;
        margin-left: 10px;
        margin-right: 0px;
        width: 228px;
        margin-top: 0;
      }
      p.live-date-time{
       display: none;
      }
        

  }